




























































import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Layout from '@/components/Layout.vue';
import Chart from '@/components/Chart.vue';
import Icons from '@/components/Icon.vue';

@Component({components: {Layout,Chart,Icons}})

export default class Echart extends Vue {
  type = '-'

  get recordList() {
    return this.$store.state.recordList;

  }
   getExpend() {
    const arr = this.recordList.filter((r: any) => r.types==='-')
    let max = arr[0].amounts || 1
    for(let i = 0;i<arr.length;i++) {
      if(max < arr[i].amounts) {
        max = arr[i].amounts
      }
    }
    for(let j = 0;j<arr.length;j++) {
      if(arr[j].amounts === max) {
        return arr[j]
      }
    }
  }
  getIncome() {
    const arr = this.recordList.filter((r: any) => r.types==='+')
    let max = arr[0].amounts
    for(let i = 0;i<arr.length;i++) {
      if(max < arr[i].amounts) {
        max = arr[i].amounts
      }
    }
    for(let j = 0;j<arr.length;j++) {
      if(arr[j].amounts === max) {
        return arr[j]
      }
    }
  }

  select(){
    if(this.type === '-') {
    this.type = '+'
    }else {
      this.type = '-'
    }
  }
  get x(){
    return {
     grid: {
        left: 30,
          right: 30,
        },
        xAxis: {
          type: 'category',
          data: ['1月','2月','3月','4月','5月','6月','7月','8月','9月','10月','11月','12月'],
          axisTick: {show: false}
        },
        yAxis: {
          type: 'value',
          show: false
        },
        series: [{
          data: [536,5678,323,967,456,346,678,123,123,456,3456,673,876],
          type: 'line',
          itemStyle: {borderColor: '#333'}
        }],
      }
    }
}
